<template>
<div>
  <!-- <q-card-section> -->
    <q-btn
      icon="graphic_eq"
      size='xl'
      flat
      disable

    >
    <div class="text-h4 ">

       A<sub>4</sub> = <span class="text-accent">{{getA4 }} </span> Hz
    </div>
     </q-btn>
  
        <canvas class="frequency-bars"></canvas>
        <div class="meter">
          <div class="meter-dot"></div>
          <div class="meter-pointer"></div>
        </div>
        <div class="notes">
          <div class="notes-list"></div>
          <div class="frequency"><span>Hz</span></div>
          <div>
            <q-btn color="accent"  @click="getFreq" label="Change Frequency" />
          </div>
        </div>
        <!-- <div class="a4" >A<sub>4</sub> = <span>{{getA4 }}</span> Hz</div> -->
      <!-- </q-card-section> -->
</div>
</template>
<script>
import { Application } from "../../middleware/tools/tuner.js"

export default {
  name: "Tuner",
  data: () => ({
    app: null
  }),
  computed: { 
    getA4(){
        return  this.app?.tuner?.middleA || '440'
    }
  },
  mounted(){
    // this.$q.dark.set(false)
    this.app = new Application()
    this.app.start()

    this.app.tuner.init()
    this.app.frequencyData = new Uint8Array(this.app.tuner.analyser.frequenyBinCount)
    console.dir(this.app)
  },
  methods: {
    setFreq(freq){
      this.app.tuner.middleA = freq
      this.app.notes.createNotes()
      this.app.update({
        name: 'A',
        frequency: this.app.a4,
        octave: 4,
        value: 69,
        cents: 0
      })
      localStorage.setItem('a4', freq)
    },
    getFreq(){
      this.$q.dialog({
        title: 'Frequency',
        message: 'What frequency would you like to tune to?',
        prompt: {
          model: this.app?.tuner.middleA || 440,
          type: 'number' // optional
        },
        cancel: true,
      }).onOk(data => {
        console.log('>>>> OK, received', data)
        this.setFreq(data)
      }).onCancel(() => {
        // console.log('>>>> Cancel')
      }).onDismiss(() => {
        // console.log('I am triggered on both OK and Cancel')
      })
   
    }
  }
}

</script>

